import styles from "./styles/Home.module.css";
import { useEffect, useMemo, useRef, useState } from "react";
import { ethers } from "ethers";
import {
    ContractAddress,
    ContractAddressMirror,
    NetworkRPC,
} from "./global";
import "./styles/tw.css";
import abi from "./abi.json";
import {
    TooltipProvider, TooltipContent, TooltipTrigger, Tooltip

} from "./Tooltip.tsx";

export default function Home() {

    const [selectedItems, setSelecteditems] = useState([])

    const handleCatClick = (event, item) => {
        const catElement = event.currentTarget;
        catElement.classList.toggle(styles.selectedCat);

        if (selectedItems.includes(item)) {
            // Remove the item
            setSelecteditems(selectedItems => selectedItems.filter(selectedItem => selectedItem !== item));
        } else {
            // Add the item
            setSelecteditems(selectedItems => [...selectedItems, item]);
        }
    };

    const [selectAll, setSelectAll] = useState(false)

    const [BullodromePrice, setBullodromePrice] = useState(0)
    const [LotteryPrice, setLotteryPrice] = useState(0)

    useEffect(() => {
        fetchBullodromePrice();
        fetchLotteryPrice();
        fetchTokenPrice();
    })

    function x() {
        console.log(selectedItems)
    }

    const selectAllCats = () => {
        setSelectAll(!selectAll)
        const catElements = document.querySelectorAll(`.${styles.yourCat}`);
        catElements.forEach(catElement => {
            if (selectAll === false) {
                catElement.classList.add(styles.selectedCat);
                setSelecteditems(inventory)
            } else {
                catElement.classList.remove(styles.selectedCat);

                setSelecteditems([])
            }
        });
    }

    const [winner, setWinner] = useState(false);

    const itemInfo = [
        {
            "cat": "1.png",
            "winrate": "0.1%",
            "name": "Montain Hunter",
            "description": "Multiply your holdings by 750% Max win 150 tokens 💰 10% immediate, 90% vested over 5 days",
        },
        {
            "cat": "2.png",
            "winrate": "1%",
            "name": "Arzuros The Bear",
            "description": "Multiply your holdings by a 200% Max win 50 tokens 💰 10% immediate, 90% vested over 7 days. ",
        },
        {
            "cat": "3.png",
            "winrate": "2%",
            "name": "F Lagia The Pirate",
            "description": "Multiply your holdings by a 50% Max win 30 tokens 💰 10% immediate, 90% vested over 10 days.",
        },
        {
            "cat": "4.png",
            "winrate": "5%",
            "name": "Shadow Boxer",
            "description": "Multiply your holdings by a 25% Max win 20 tokens 💰 10% immediate, 90% vested over 10 days.",
        },
        {
            "cat": "5.png",
            "winrate": "7%",
            "name": "Dragon Hunter",
            "description": "Use to have a 25% chance of minting you 1 new cats, 75% of give you nothing and coming back as villager (you pay a lottery fee to have a 25% chance of getting 1 extra cats) ",
        },
        {
            "cat": "6.png",
            "winrate": "34.5%",
            "name": "Bullodrome Hunter",
            "description": "lottery entry to 0.0075ETH (-25%)",
        },
        {
            "cat": "7.png",
            "winrate": "50.4%",
            "name": "Village retard",
            "description": "Provides substantial benefits deemed sufficiently powerful as is. (does nothing)",
        },
        {
            "cat": "0.png",
            "winrate": "100%",
            "name": "Villager Cat",
            "description": "Basicly your default cat, you got him when you buy tokens, your main goal is to equip him with armor to send him to quests. Nothing special, just an average villager NPC",
        }
    ]
    const [inventory, setInventory] = useState([]);
    const [vested, setVested] = useState(0);
    const [tokenPrice, setTokenPrice] = useState(0);
    const [walletText, setWalletText] = useState("CONNECT WALLET")

    const lastSelectedCat = useMemo(() => {
        if (selectedItems.length > 0)
            return selectedItems[selectedItems.length - 1];
    }, [selectAll, selectedItems]);

    const areInventoriesDifferent = (oldInv, newInv) => {
        if (oldInv.length !== newInv.length) return true;
        
        for (let i = 0; i < oldInv.length; i++) {
          if (
            oldInv[i].tokenId !== newInv[i].tokenId ||
            oldInv[i].god !== newInv[i].god
          ) {
            return true; // Found a difference, break early
          }
        }
        return false; // No differences found
    };

    async function fetchNFTs(accounts) {
        if (accounts.length > 0) {
            const oldInv = inventory;

            setInventory([]);
            setVested(
                Number(await getVestedAmount(accounts[0])) / 1_000_000_000_000_000_000
            );
            setWinner(await checkWinner(accounts))
            const reponse = await fetch(
                `https://api.basescan.org/api?module=account&action=tokennfttx&contractaddress=${ContractAddressMirror}&address=${accounts[0]}&page=1&offset=4040&startblock=0&endblock=99999999&sort=asc&apikey=PV6T85B8RE9HEVEXYTEN9XHYXPZVQWNWBX`,
                {
                    method: "GET",
                    headers: {
                        accept: "application/json",
                    },
                }
            );
            const data = await reponse.json();
            let tokens = [];
            if (!data) {
                return;
            }
            if (data.message === "NOTOK") {
                return;
            }

            if (data.message === "OK" && data.result) {
                data.result.map((trx) => {
                    if (trx.to === accounts[0]) {
                        tokens.push(Number(trx.tokenID));
                    } else {
                        var index = tokens.findIndex((v) => v === Number(trx.tokenID));
                        if (index !== -1) {
                            tokens.splice(index, 1);
                        }
                    }
                });

                if (tokens.length > 0) {
                    for await (const n of tokens) {
                        while (true) {
                            try {
                                const response = await getTokenURI(n);
                                const json = JSON.parse(
                                    response.replace("data:application/json;utf8,", "")
                                );

                                let god;
                                if (json.image.endsWith("0.png")) {
                                    god = "cat0.png";
                                } else if (json.image.endsWith("1.png")) {
                                    god = "cat1.png";
                                } else if (json.image.endsWith("2.png")) {
                                    god = "cat2.png";
                                } else if (json.image.endsWith("3.png")) {
                                    god = "cat3.png";
                                } else if (json.image.endsWith("4.png")) {
                                    god = "cat4.png";
                                } else if (json.image.endsWith("5.png")) {
                                    god = "cat5.png";
                                } else if (json.image.endsWith("6.png")) {
                                    god = "cat6.png";
                                } else if (json.image.endsWith("7.png")) {
                                    god = "cat7.png";
                                }

                                // Update the inventory state incrementally
                                setInventory((prevInv) => [...prevInv, { tokenId: n, god }]);
                                break
                            }
                            catch {

                            }
                        }
                    }
                }

            }
        }
    }

    async function checkWinner(accounts) {
        if (accounts.length > 0) {
            try {
                const accounts = await window.ethereum.request({
                    method: "eth_requestAccounts",
                });
                if (accounts.length === 0) {
                    return;
                }

                const provider = new ethers.JsonRpcProvider(NetworkRPC);
                const contractABI = abi;

                const contract = new ethers.Contract(
                    ContractAddress,
                    contractABI,
                    provider
                );
                const data = await contract.hasClaim(accounts[0]) >= 1n;
                console.log(data)
                return data;
            } catch (error) {
                return false;
            }
        }
    }

    const jackpot = useMemo(
        () => inventory.length * 7.5 * tokenPrice,
        [inventory.length, tokenPrice]
    );

    async function fetchTokenPrice() {
        try {
            const res = await fetch(
                `https://api.dexscreener.com/latest/dex/pairs/base/0x570492bB9853B39eD35047Ff2cF942E45E7ec226`
            );
            const json = await res.json();
            setTokenPrice(Number(json["pairs"][0]["priceUsd"] ?? 0));
        } catch (e) {
            console.error(e);
        }
        return 0;
    }

    async function fetchBullodromePrice() {
        try {
            const provider = new ethers.JsonRpcProvider(NetworkRPC);
            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                provider
            );
            const data = await contract.Bullodrome();
            setBullodromePrice(Number(data) / 1_000_000_000_000_000);
        } catch (error) {
            setBullodromePrice(0)
        }
    }

    async function fetchLotteryPrice() {
        try {
            const provider = new ethers.JsonRpcProvider(NetworkRPC);
            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                provider
            );
            const data = await contract.Lottery();
            setLotteryPrice(Number(data) / 1_000_000_000_000_000)
        } catch (error) {
            setLotteryPrice(0)
        }
    }

    async function getTokenURI(tokenId) {
        try {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });
            if (accounts.length === 0) {
                return;
            }
            const provider = new ethers.JsonRpcProvider(NetworkRPC);
            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                provider
            );
            const data = await contract.tokenURI(tokenId);
            return data;
        } catch (error) {
            // ignored
            console.log(error)
        }
    }

    async function connect() {
        if (!window.ethereum) {
            return;
        }

        try {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });
            fetchNFTs(accounts);
            setWalletText(`${accounts[0].slice(0, 5)}...${accounts[0].slice(-5)}`)
            window.ethereum.on("accountsChanged", fetchNFTs);
        } catch (err) {
            console.log(err)
            //ignored
        }
    }

    async function claimWin() {
        try {
            const accounts = await window.ethereum.request({
                method: "eth_accounts",
            });
            if (accounts.length === 0) {
                return;
            }

            const metaMaskProvider = new ethers.BrowserProvider(window.ethereum);
            const signer = await metaMaskProvider.getSigner(accounts[0]);

            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                signer
            );
            const tx = await contract.claim();
            await tx.wait();
            setSelecteditems([]);
            await fetchNFTs(accounts);
        } catch (error) {
            // ignored
            console.log(error);
        }
    }

    async function claimVestedWin() {
        try {
            const accounts = await window.ethereum.request({
                method: "eth_accounts",
            });
            if (accounts.length === 0) {
                return;
            }

            const metaMaskProvider = new ethers.BrowserProvider(window.ethereum);
            const signer = await metaMaskProvider.getSigner(accounts[0]);

            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                signer
            );
            const tx = await contract.ClaimVested();
            await tx.wait();
            setSelecteditems([]);
            await fetchNFTs(accounts);
        } catch (error) {
            // ignored
            console.log(error);
        }
    }

    async function getVestedAmount(addr) {
        try {
            const provider = new ethers.JsonRpcProvider(NetworkRPC);
            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                provider
            );
            const data = await contract.ReadVested(addr);
            return data;
        } catch (error) {
            return 0n;
        }
    }

    async function lottery(tokens) {
        try {
            const accounts = await window.ethereum.request({
                method: "eth_accounts",
            });
            if (accounts.length === 0) {
                return;
            }

            const metaMaskProvider = new ethers.BrowserProvider(window.ethereum);
            const signer = await metaMaskProvider.getSigner(accounts[0]);

            const contractABI = abi;

            const contract = new ethers.Contract(
                ContractAddress,
                contractABI,
                signer
            );

            let sum = 0;
            for (const token of tokens) {
                if (token.god === "cat6.png") {
                    sum += BullodromePrice;
                } else {
                    sum += LotteryPrice;
                }
            }

            const tokenIds = tokens.filter((x) => x.tokenId).map((x) => x.tokenId);
            console.log(tokenIds)
            console.log(sum)
            const tx = await contract.lottery(
                tokenIds,
                { value: ethers.parseEther(String(sum / 1000)) }
            );

            await tx.wait();
            setSelecteditems([]);
            await fetchNFTs(accounts);
        } catch (error) {
            console.log(error)
        }
    }
    const winAudioRef = useRef();

    useEffect(() => {
        if (winner)
            winAudioRef?.current.play();
    }, [winner]);

    useEffect(() => {
        if (winAudioRef)
            winAudioRef.current.volume = 0.2;
    }, [winAudioRef]);

    return (
        <div className="overflow-x-clip w-full">
            <audio src="./win.mp3" ref={winAudioRef} />
            <TooltipProvider>
                <div className={`${styles.wrapper} overflow-x-clip`}>
                    <div className={`fixed right-0 -bottom-20 transition-all duration-150 ease-in-out ${winner ? "translate-x-[145px]" : "translate-x-[620px]"}`}>
                        <div className="relative w-full">
                            <img src="./images/won-flag.png" alt="you-win" className={"left-0 top-0"} />
                            <p className="z-10 text-white font-lora text-lg absolute left-[180px] top-[55px]">You win!</p>
                        </div>
                    </div>
                    <header className={styles.header}>
                        <div className={styles.header__container}>
                            <a href="/" className={styles.header__logo}>
                                <img src="./images/logo.png" alt="monster hunter" className={styles.header__logoImage} />
                            </a>
                            <nav className={styles.header__nav}>
                                <a href="/#home" className={styles.header__link}>
                                    HOME
                                </a>
                                <a onClick={() => {window.open('https://docs.monsterhunterkawai.xyz/', '_blank')}} className={styles.header__link}>
                                    DOCS
                                </a>
                                <button onClick={() => { connect() }} className={styles.connect_wallet}>
                                    {walletText}
                                </button>
                            </nav>
                        </div>
                    </header>
                    <main className={styles.main}>
                        <section className={styles.main__cats_header}>
                            <TooltipProvider>
                                <Tooltip >
                                    <TooltipTrigger asChild>Hover</TooltipTrigger>
                                    <TooltipContent>
                                        <p>Add to library</p>
                                    </TooltipContent>
                                </Tooltip>
                            </TooltipProvider>

                            <ul className={styles.main__cats_list}>
                                {
                                    itemInfo.map(item => {
                                        return (
                                            <Tooltip>
                                                <TooltipTrigger>
                                                    <li className={`relative ${styles.main__cat_item} ${styles.selectedCat}`}>
                                                    <TooltipContent className="relative pointer-events-none z-10">
                                                    <p className="text-lg font-lora bg-black bg-opacity-75 rounded-lg p-2 text-white left-0 top-10 absolute" style={{ width: '300px' }}>
                                                        {item.description}
                                                    </p>
                                                    </TooltipContent>


                                                        <img src={`./images/cats-header/cat${item.cat}`} alt={item.cat} className={styles.cat__headerImage} />
                                                        <h4 className={styles.cat__title}>
                                                            {item.name}
                                                        </h4>
                                                        <span className={styles.cat__percent}>
                                                            {item.winrate}
                                                        </span>
                                                    </li>
                                                </TooltipTrigger>
                                            </Tooltip>
                                        )
                                    })
                                }
                            </ul>
                        </section>
                        <section className={styles.sectionSecond1}>
                            <div className={styles.jackpotContainer}>
                                <img src="./images/jackpot-line.svg" alt="" className={styles.jackpotLineLeft} />
                                <div className={styles.jackpotText} style={{ backgroundImage: "url('./images/jackpot-bg.png')" }}>Your potential cat Jackpot {jackpot.toFixed(2)}$</div>
                                <img src="./images/jackpot-line.svg" alt="" className={styles.jackpotLineRight} />
                            </div>
                            <section className={`grid grid-cols-4 ${styles.sectionSecond}`}>
                                <section className={`col-span-3 ${styles.catsAndInventory}`} style={{ backgroundImage: "url('./images/your-cats/background.png')" }}>
                                    <ul className={styles.yourCatsList}>
                                        {inventory.map(item => {
                                            return (
                                                <li className={styles.yourCat} onClick={(e) => { handleCatClick(e, item); }}>
                                                    <img src={`./images/your-cats/${item.god}`} alt="" className={styles.yourCat__image} />
                                                    <div className={styles.yourCatShadowAndText}>
                                                        <img src="./images/your-cats/white-bottom-shadow.svg" alt="" className={styles.yourCat__shadow} />
                                                    </div>
                                                    <h2 className={styles.yourCat__title}>
                                                        CAT #{item.tokenId}
                                                    </h2>
                                                </li>
                                            )
                                        })}
                                    </ul>
                                </section>
                                <div className={`col-span-1 ${styles.rightSection3}`}>
                                    {
                                        winner === true && <div className={styles.gameResult}>
                                            <span> You win!</span>
                                        </div>
                                    }
                                    {
                                        vested > 0 && (
                                            <>
                                                <div className={styles.claimVestedTest} style={{ backgroundColor: "transparent", textAlign: "center", width: "100%" }}>Unlocked Vested: {vested.toFixed(6)} $KAWAI</div>

                                                <div className={styles.button__container}>
                                                    <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration} />
                                                    <button className={styles.button} style={{ width: "100%" }} onClick={() => { claimVestedWin() }}>Claim Vested</button>
                                                    <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration2} />
                                                </div>
                                            </>
                                        )
                                    }
                                    {
                                        winner === true && (
                                            <div className={styles.button__container}>
                                                <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration} />
                                                <button className={styles.button} style={{ width: "100%" }} onClick={() => { claimWin() }}>Claim Win</button>
                                                <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration2} />
                                            </div>)
                                    }
                                    {<div className="flex flex-col items-center">
                                        <img src={`./images/your-cats/${lastSelectedCat?.god}`} alt={lastSelectedCat?.god} className={`transition-all duration-150 ease-in-out ${lastSelectedCat == null ? "scale-0" : "scale-125"}`} />
                                        {/* <p className="mx-auto text-lg text-white font-lora text-center"> */}
                                        <p className={`pl-10 ${styles.inventoryText}`}>
                                            {lastSelectedCat ?
                                                '' :
                                                "Select the cat that you want to play and click on play to Win."}
                                        </p>
                                    </div>
                                    }
                                </div>
                            </section>
                        </section>
                        <section className={`w-full pl-20 ${styles.buttons}`}>
                            <div className={styles.button__container}>
                                <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration} />
                                <button className={styles.button} onClick={selectAllCats}>{!selectAll ? 'Select all' : 'Unselect all'}</button>
                                <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration2} />
                            </div>
                            <div className={styles.button__container}>
                                <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration} />
                                <button className={styles.button} onClick={() => { lottery(selectedItems) }}>Play now</button>
                                <img src="./images/button/button-decoration.svg" alt="" className={styles.button__decoration2} />
                            </div>
                        </section>
                    </main>
                </div >
            </TooltipProvider>
        </div>
    );
}